
body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Source Sans Pro','Helvetica Neue',Helvetica,Arial,sans-serif;font-weight:400;overflow-x:hidden;overflow-y:auto;min-height:100%;font-size:14px;line-height:1.42857143;color:#333;background-color:#fff;margin:0
}
*{box-sizing:border-box
}
.wrapper{min-height:100%;position:relative;overflow:hidden
}
.transparentBg{background-color:transparent
}
.main-header{position:relative;max-height:100px;z-index:1030
}
.main-header a{text-decoration:none;cursor:pointer
}
.main-header .nav:before{clear:both
}
.main-header .navbar{transition:margin-left 0.3s ease-in-out;margin-bottom:0;margin-left:230px;border:none;min-height:60px;border-radius:0;background-color:#3c8dbc
}
.main-header .navbar .navbar-right-menu{float:right;background-color:#3c8dbc
}
.main-header .navbar li span{color:#fff
}
.main-header .navbar a:hover,.main-header .navbar li:hover{background:#367fa9
}
.main-header .navbar .el-menu--horizontal .el-menu-item:hover,.main-header .navbar .el-menu--horizontal .el-submenu__title:hover{background:#367fa9
}
.main-header .logo{transition:width .3s ease-in-out;display:block;float:left;height:60px;font-size:20px;line-height:60px;text-align:center;width:230px;font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;font-weight:300;overflow:hidden;color:#fff;background-color:#367fa9
}
.main-header .logo .logo-lg{display:block
}
.main-header .logo .logo-mini{display:none
}
.main-header .sidebar-toggle{float:left;background-color:transparent;background-image:none;padding:20px 20px;font-family:fontAwesome;color:#fff
}
.main-header .sidebar-toggle .sr-only{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);border:0
}
.main-header .sidebar-toggle:before{content:"\f0c9"
}
.main-sidebar{position:absolute;top:0px;left:0px;padding-top:60px;min-height:100%;width:230px;z-index:810;transition:transform 0.3s ease-in-out, width 0.3s ease-in-out;transition:none;background-color:#222d32
}
.main-sidebar .sidebar{padding-bottom:10px;height:auto
}
.main-sidebar .sidebar-form{border-radius:3px;border:1px solid #374850;margin:10px 10px
}
.main-sidebar .user-panel{position:relative;width:100%;padding:10px;overflow:hidden
}
.main-sidebar .user-panel .image img{width:100%;max-width:45px;height:auto
}
.main-sidebar .user-panel .info{padding:5px 5px 5px 15px;line-height:1;position:absolute;left:55px
}
.main-sidebar .user-panel .info p{font-weight:600;margin:0 0 10px;color:#fff
}
.main-sidebar .user-panel .info a{text-decoration:none;padding-right:5px;margin-top:3px;font-size:11px;color:#fff
}
.main-sidebar .img-circle{border-radius:50%
}
.main-sidebar img{vertical-align:middle;border:0
}
.main-sidebar .user-panel:before,.main-sidebar .user-panel:after{content:" ";display:table
}
.main-sidebar .sidebarmenu,.main-sidebar .user-panel{white-space:nowrap;overflow:hidden
}
.main-sidebar .sidebarmenu{list-style:none;margin:0px;padding:0px
}
.clearfix:after,.clearfix:before,.nav:after,.nav:before,.navbar-collapse:after,.navbar-collapse:before,.navbar-header:after,.navbar-header:before,.navbar:after,.navbar:before{clear:both
}
:after,:before{box-sizing:border-box
}



