
.content-wrapper{min-height:100%;background-color:#ecf0f5;transition:transform .3s ease-in-out,margin .3s ease-in-out;margin-left:230px;z-index:820
}
.content-wrapper .content-header{position:relative;padding:15px 15px 0 15px
}
.content-wrapper .content-header h1{margin:0;font-size:24px
}
.content-wrapper .content-header h1 small{font-size:15px;display:inline-block;padding-left:4px;font-weight:300;line-height:1;color:#777
}
.content-wrapper .content-header .breadcrumb{margin-top:-15px;margin-bottom:10px
}
.content-wrapper .content-header .el-breadcrumb__item__text i:after{content:' ';margin-right:10px
}
.content-wrapper .content{min-height:250px;padding:15px;margin-right:auto;margin-left:auto;padding-left:15px;padding-right:15px
}



