
.main-sidebar{position:absolute;top:0px;left:0px;padding-top:60px;min-height:100%;width:230px;z-index:810;transition:transform 0.3s ease-in-out, width 0.3s ease-in-out;transition:none;background-color:#222d32
}
.main-sidebar .sidebar{padding-bottom:10px;height:auto
}
.main-sidebar .sidebar .sidebar-form{border-radius:3px;border:1px solid #374850;margin:10px 10px
}
.main-sidebar .sidebar .user-panel{position:relative;width:100%;padding:10px;overflow:hidden
}
.main-sidebar .sidebar .user-panel .image img{width:100%;max-width:45px;height:auto
}
.main-sidebar .sidebar .user-panel .info{padding:5px 5px 5px 15px;line-height:1;position:absolute;left:55px
}
.main-sidebar .sidebar .user-panel .info p{font-weight:600;margin:0 0 10px;color:#fff
}
.main-sidebar .sidebar .user-panel .info a{text-decoration:none;padding-right:5px;margin-top:3px;font-size:11px;color:#fff
}
.main-sidebar .img-circle{border-radius:50%
}
.main-sidebar img{vertical-align:middle;border:0
}
.main-sidebar .user-panel:before,.main-sidebar .user-panel:after{content:" ";display:table
}
.main-sidebar .sidebarmenu,.main-sidebar .user-panel{white-space:nowrap;overflow:hidden
}
.main-sidebar .sidebarmenu{list-style:none;margin:0px;padding:0px
}
.main-sidebar li a{display:block;color:#a7b1c2;font-weight:600;padding:0px 0px 5px 0px;text-decoration:none
}
.sidebar-form input[type="text"],.sidebar-form .btn{box-shadow:none;background-color:#374850;border:1px solid transparent;height:35px
}
.sidebar-form input[type="text"]{color:#666;border-top-left-radius:2px;border-top-right-radius:0;border-bottom-right-radius:0;border-bottom-left-radius:2px
}
.sidebar-form input[type="text"]:focus,.sidebar-form input[type="text"]:focus+button{background-color:#fff;color:#666
}
.sidebar-form input[type="text"]:focus+button{border-left-color:#fff
}
.sidebar-form button{color:#999;border-top-left-radius:0;border-top-right-radius:2px;border-bottom-right-radius:2px;border-bottom-left-radius:0
}
.sidebar .el-input-group__append{border-color:transparent;background-color:transparent
}
.sidebar .el-menu-item,.el-submenu__title{height:44px;line-height:44px
}
.sidebar-menu li>a>.pull-right-container{position:absolute;right:10px;top:50%;margin-top:-7px
}



